import React,{useContext}from 'react'
import './Play.component.scss';
import Markdown from 'markdown-to-jsx';
import SectionH2 from '../../components/SectionH2/Sectionh2.component';
import Wrapper from '../../components/Wrapper/Wrapper.component.jsx';
import Container from '../../components/Container/Container.component.jsx';
import ContentArea from '../../components/ContentArea/ContentArea.component.jsx';
import { AppContext } from '../../AppContext';

export default function Play({title, copy}) {
  const { isMD, isLG } = useContext(AppContext);
  const columns = isLG ? `repeat(12,1fr)` : isMD ? `repeat(8,1fr)` : `repeat(4,1fr)`;

  return (
    <Wrapper>
      <Container 
        display={`flex`} 
        maxWidth={`768px`} 
        // columns={columns}
        classe={`s-container`}
      >
          {/* <ContentArea classe={`play`}> */}
              <SectionH2 h2={title} />
              <p>
                <Markdown>
                  {copy}
                </Markdown>
              </p>
          {/* </ContentArea> */}
        </Container>
    </Wrapper>
  )
}
